<template>
  <div>
    <p class="text-center font-weight-bold mt-8">{{ content.entryname }}</p>
    <p class="text-center grey--text .body-2 mt-1">
      <span class="mr-3">发布高校: {{ content.uploads }}</span>
      <span class="mx-3">发布时间：{{ content.time }}</span>
      <span class="ml-3">点击量: {{ content.click }}</span>
    </p>
    <div class="dotted_line mt-3 mx-5"></div>
    <p class="text-left mx-12 mt-2">
      <span class="text-left font-weight-bold">成果负责人</span>
      <span class="pl-2">{{ content.charge }}</span>
    </p>
    <p class="text-left mx-12 mt-2">
      <span class="text-left font-weight-bold">负责人职称</span>
      <span class="pl-2">{{ content.position }}</span>
    </p>
    <p class="text-left mx-12 mt-2">
      <span class="text-left font-weight-bold">成果简介：</span>
      <span>
        {{ content.results }}
      </span>
    </p>
    <div class="mx-12">
      <span class="text-left font-weight-bold" v-if="content.tecNew"
        >技术创新点：</span
      >
      <p v-html="content.tecNew">{{ content.tecNew }}</p>
    </div>
    <div class="mx-12">
      <span class="text-left font-weight-bold" v-if="content.future"
        >市场前景及应用：</span
      >
      <p v-html="content.future">{{ content.future }}</p>
    </div>
    <p class="text-left mx-12 mt-2">
      <span class="text-left font-weight-bold" v-if="content.maturity"
        >技术成熟度：</span
      >
      <span>
        {{ content.maturity }}
      </span>
    </p>
    <p class="text-left mx-12 pt-4" v-if="content.fileurl">
      <v-icon>mdi-download</v-icon><span class="text-left">附件下载：</span>
      <a :href="content.fileurl" :download="filename">{{ filename }}</a>
    </p>
    <div class="text-center mt-3">
      <div class="text-center">
        <v-dialog v-model="dialog" width="500">
          <template v-slot:activator="{ on }">
            <v-btn color="indigo" dark v-on="on" min-width="300"
              >项目负责人联系方式</v-btn
            >
          </template>

          <v-card>
            <v-card-title class="grey lighten-2" primary-title
              >联系方式</v-card-title
            >

            <v-card-text class="mt-4 body-1">
              <p>
                <span>联系人姓名：</span>
                {{ content.contacts }}
              </p>
              <p>
                <span>联系电话：</span>
                {{ content.tel }}
              </p>
              <p>
                <span>联系邮箱：</span>
                {{ content.mail }}
              </p>
            </v-card-text>

            <v-divider></v-divider>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="primary" text @click="dialog = false">确认</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </div>
    </div>
    <div class="dotted_line mt-3 mx-5"></div>
    <div class="ml-8 mt-8">
      <p
        @click="handleToDetail(last.id || '')"
        style="cursor: pointer;"
        class="lastAndNext"
      >
        <span class="grey--text">上一条：</span>
        {{ last.entryname || '' }}
      </p>
      <p
        @click="handleToDetail(next.id || '')"
        style="cursor: pointer;"
        class="lastAndNext"
      >
        <span class="grey--text">下一条：</span>
        {{ next.entryname || '' }}
      </p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'detail',
  props: ['subject', 'id'],
  data() {
    return {
      last: {
        id: '',
        entryname: '',
      },
      next: {
        id: '',
        entryname: '',
      },
      content: {
        entryname: '',
        results: '',
        tecNew: null,
        future: null,
        contacts: '',
        mail: '',
        tel: '',
        position: '',
        maturity: '',
        charge: '',
      },
      filename: '',
      dialog: false,
    }
  },
  watch: {
    $route() {
      this.getSci()
    },
  },
  methods: {
    isUndef(obj) {
      if (obj === undefined) {
        return true
      } else return false
    },
    async getSci() {
      var school = this.$route.query.school
      if (this.isUndef(this.$route.query.school)) {
        school = ''
      }
      var year = this.$route.query.year
      if (this.isUndef(this.$route.query.year)) {
        year = ''
      }
      if (!this.innerSubject) {
        this.innerSubject = 'æœºæ¢°'
      }
      let res = await this.$http.get(
        '/api/business/readSci?id=' +
          this.id +
          '&config=' +
          this.subject +
          '&school=' +
          school +
          '&year=' +
          year
      )
      if (res.status != 200) return false
      if (res.data.code != 200) return false
      this.content = res.data.data.Sci
      this.filename = res.data.data.filename
      if (res.data.data.Scilast) {
        this.last.entryname = res.data.data.Scilast.entryname
        this.last.id = res.data.data.Scilast.id
      }
      if (res.data.data.Scinext) {
        this.next.entryname = res.data.data.Scinext.entryname
        this.next.id = res.data.data.Scinext.id
      }
    },
    handleToDetail(id) {
      this.$router.push({
        path: '/company/research/list/' + this.subject + '/detail/' + id,
        query: {
          school: this.$route.query.school,
          year: this.$route.query.year,
        },
      })
    },
  },
  mounted() {
    this.getSci()
  },
}
</script>

<style scoped>
.dotted_line {
  width: 98%;
  height: 0;
  border-bottom: #c6c6c6 1px dashed;
}
.lastAndNext:hover {
  color: #e53935;
}
a:link {
  color: #1976d2;
}
a:hover {
  color: #e53935;
}
</style>
